.coin{
    margin-top: 10px;
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    font-size: 10px;
    align-items: center;
    border-bottom: 1px solid grey;
}
.logo{
    display: flex;
    align-items: center;
}
.logo img{
    border-radius: 50%;
    margin: 0 auto;
    display: block;
    width: 65px;
}